import * as ethers from 'ethers/index.js';
import Config from './config.json'



function toHumanReadable(abi) {
    if (!Array.isArray(abi)) { return }
    abi = abi.filter(v => v.type !== 'fallback')
    const hrabi = abi.map(v => {
        const type = v.type
        const name = v.name ? ' ' + v.name : ''
        const inputs = v.inputs ? v.inputs.map(i => i.type + (i.indexed ? ' indexed' : '') + (i.name ? ' ' + i.name : '')).join() : ''
        const stateMutability = v.stateMutability ? (v.stateMutability !== "nonpayable" ? ' ' + v.stateMutability : '') : ''
        const returns = v.outputs ? (v.outputs.length >= 0 ? ` returns (${v.outputs.map((i) => i.type + (i.name ? ' ' + i.name : '')).join()})` : '') : ''
        return `${type}${name}(${inputs})${stateMutability}${returns}`;
    })
    return JSON.stringify(hrabi, null, 2);
}
function toAbi(hrabi) {
    if (!Array.isArray(hrabi)) { return }
    let abi = new ethers.utils.Interface(hrabi).abi.map(abi => {
        abi = JSON.parse(JSON.stringify(abi));
        delete abi.gas;
        if (abi.stateMutability === null) {
            delete abi.stateMutability
        }
        return abi
    })
    return JSON.stringify(abi, null, 2);
}

function erc20(tokenAddress, provider) {
    let contract = new ethers.Contract(tokenAddress, Config.erc20.abi, provider);
    return contract;
}

async function deployErc20Token(name, symbol, decimals, wallet) {
    let erc20 = new ethers.ContractFactory(Config.erc20.abi, Config.erc20.bytecode);
    erc20 = erc20.connect(wallet);
    let contract = await erc20.deploy(name, symbol, decimals);
    return contract
}

async function deployMutiWalletToken(_owners, _required, _daylimit, wallet) {
    let mutiWallet = new ethers.ContractFactory(Config.mutiWallet.abi, Config.mutiWallet.bytecode);
    mutiWallet = mutiWallet.connect(wallet);
    let contract = await mutiWallet.deploy(name, symbol, decimals);
    return contract
}


function Event(contract, eventName, args = [], fromBlock = 0, toBlock = 'latest') {
    args = Array.isArray(args) ? args : [args];
    let event = contract.filters[eventName](...args);
    let abi = contract.interface.abi.find((v) => v.name === eventName).inputs;

    let filter = {
        fromBlock,
        toBlock,
        ...event
    }


    return this.getLogs(filter).then(function (result) {
        let coder = new ethers.utils.AbiCoder()

        let abi1 = abi.filter((v) => v.indexed)
        let abi2 = abi.filter((v) => !v.indexed)

        let r = result.map(r => {
            r.topics.shift()
            let topics = r.topics.map((t, i) => {
                return coder.decode([abi1[i]], t)[0]
            })
            let datas = coder.decode(abi2, r.data)
            let _r = abi.map(v => v.indexed ? topics.shift() : datas.shift())
            _r.transactionHash = r.transactionHash
            _r.blockNumber = Number(r.blockNumber)
            return _r
        })

        return r
    })

    return Promise.resolve(1)
}


export default {
    toAbi,
    toHumanReadable,
    deployErc20Token,
    deployMutiWalletToken,
    erc20,
    Event
}

