import React, { Component } from 'react';
import config from './config.json';
import { Link } from 'react-router-dom';
import { observer, inject } from 'mobx-react';
import * as ethers from 'ethers/index.js';
import Config from '../../../contract/config.json'
import { Event } from '../../../contract/index'
import dayjs from 'dayjs';

@inject('ethereumStore')
@observer
export default class Inbox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            recipients: [],
            ccs: [],
            sends: []
        }
        const ethMail = Config.ethMail;
        this.contract = new ethers.Contract(ethMail[this.props.ethereumStore.networkId].contractAddress, ethMail.abi, this.props.ethereumStore.provider);
    }
    componentWillMount = async () => {
        let event = Event.bind(this.props.ethereumStore.provider);
        let [SendMailToRecipient, SendMailToCC, AuthorSendMail] = await Promise.all([
            event(this.contract, 'SendMailToRecipient', [null, this.props.ethereumStore.address]),
            event(this.contract, 'SendMailToCC', [null, this.props.ethereumStore.address]),
            event(this.contract, 'AuthorSendMail', [null, this.props.ethereumStore.address]),
        ]);
        SendMailToRecipient = await Promise.all(SendMailToRecipient.map(v => event(this.contract, 'MailInfo', [v[0]])))
        SendMailToCC = await Promise.all(SendMailToCC.map(v => event(this.contract, 'MailInfo', [v[0]])))
        AuthorSendMail = await Promise.all(AuthorSendMail.map(v => event(this.contract, 'MailInfo', [v[0]])))
        console.log(SendMailToRecipient, SendMailToCC, AuthorSendMail)
        this.setState({
            recipients: [].concat(...SendMailToRecipient),
            ccs: [].concat(...SendMailToCC),
            sends: [].concat(...AuthorSendMail),
        })
    }
    

    render() {
        let recipients = this.state.recipients.map((v, index) => {
            return (
                <tr key={v[0]} >
                    <td>{index + 1}</td>
                    <td>{v[1]}</td>
                    <td>{v[2]}</td>
                    <td>{dayjs(v[3].toNumber() * 1000).format('YYYY年MM月DD日 HH:mm:ss')}</td>
                    <td><Link to={"/dapp/ethMail/reading?mailId=" + v[0]}
                        onClick={() => {
                            this.props.ethMail.setObject({
                                mailId: v[0],
                                send: v[1],
                                title: v[2],
                                body: v[4],
                            })
                        }}

                    >點擊我</Link></td>
                </tr>
            )
        })
        // let ccs = this.state.ccs.map((v, index) => {
        //     return (
        //         <tr key={v[0]} >
        //             <td>{index + 1}</td>
        //             <td>{v[1]}</td>
        //             <td>{v[2]}</td>
        //             <td>{dayjs(v[3].toNumber() * 1000).format('YYYY年MM月DD日 HH:mm:ss')}</td>
        //             <td><Link to={"/dapp/ethMail/reading?mailId=" + v[0]}
        //                 onClick={() => {
        //                     this.props.ethMail.setObject({
        //                         mailId: v[0],
        //                         send: v[1],
        //                         title: v[2],
        //                         body: v[4],
        //                     })
        //                 }}

        //             >點擊我</Link></td>
        //         </tr>
        //     )
        // })
        let sends = this.state.sends.map((v, index) => {
            return (
                <tr key={v[0]} >
                    <td>{index + 1}</td>
                    <td>{v[1]}</td>
                    <td>{v[2]}</td>
                    <td>{dayjs(v[3].toNumber() * 1000).format('YYYY年MM月DD日 HH:mm:ss')}</td>
                    <td><Link to={"/dapp/ethMail/reading?mailId=" + v[0]}
                        onClick={() => {
                            this.props.ethMail.setObject({
                                mailId: v[0],
                                send: v[1],
                                title: v[2],
                                body: v[4],
                            })
                        }}

                    >點擊我</Link></td>
                </tr>
            )
        })
        return (
            <div>
                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">收件夾</h3>
                    </div>
                    <div className="box-body">
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <th style={{ "width": "10px" }}>#</th>
                                    <th>寄件者</th>
                                    <th>標題</th>
                                    <th>時間</th>
                                    <th>閱讀</th>
                                </tr>
                            </thead>
                            <tbody>
                                {recipients}
                            </tbody>
                        </table>
                    </div>
                    <div className="box-footer"> </div>
                </div>
                {/* <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">CC</h3>
                    </div>
                    <div className="box-body">
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <th style={{ "width": "10px" }}>#</th>
                                    <th>寄件者</th>
                                    <th>標題</th>
                                    <th>時間</th>
                                    <th>閱讀</th>
                                </tr>
                            </thead>
                            <tbody>
                                {ccs}
                            </tbody>
                        </table>
                    </div>
                    <div className="box-footer"> </div>
                </div> */}
                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">已寄出</h3>
                    </div>
                    <div className="box-body">
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <th style={{ "width": "10px" }}>#</th>
                                    <th>寄件者</th>
                                    <th>標題</th>
                                    <th>時間</th>
                                    <th>閱讀</th>
                                </tr>
                            </thead>
                            <tbody>
                                {sends}
                            </tbody>
                        </table>
                    </div>
                    <div className="box-footer"> </div>
                </div>
            </div>
        )
    }
}
export { Inbox };